<template lang="html">
  <div class="register-vue">
  <div class="main-part">
    <div class="ope">
      <a class="normal"
      v-bind:class="{'active':isLog}"
      v-on:click="setLog">ÁôªÂΩï</a>
      <b class="point">¬∑</b>
      <a class="normal"
      v-bind:class="{'active':!isLog}"
        v-on:click="setReg">Ê≥®ÂÜå</a>
    </div>

  <div v-show="isLog" class = "if-class" :class="{'move-lvzu':isLog}">
    <div class="login-input">
      <div class="us-pw-input-pre">
        <svg class="icon bef-icon" aria-hidden="true">
          <use xlink:href="#icon-yonghuming1"></use>
        </svg>
        <input class = "us-pd-input" maxlength="11"  spellcheck="false" v-model="log_username" placeholder="ÊâãÊú∫Âè∑">
      </div>
      <div class="us-pw-input-pre">
        <svg class="icon bef-icon" aria-hidden="true">
          <use xlink:href="#icon-mima"></use>
        </svg>
        <input class = "us-pd-input" maxlength="16" type = 'password' v-model="log_password" placeholder="ÂØÜÁ†Å">
      </div>
    </div>
    <div class="clarify">
      <el-checkbox v-model="remember" ></el-checkbox>
      <span class="remember"> ËÆ∞‰ΩèÊàë </span>
      <span class= "login-problem">ÁôªÂΩïÈÅáÂà∞ÈóÆÈ¢ò?</span>
    </div>
    <div class="button">
      <el-button class="login-button" type="info" size='large' @click="_login">Áôª ÂΩï</el-button>
    </div>
    <div class="others">
      <h6 class="social-text">Á§æ‰∫§Ë¥¶Âè∑ÁôªÂΩï</h6>
    </div>
    <div class="other-icons">
      <svg class="icon icons" aria-hidden="true" v-for="i in 4">
        <use v-bind:xlink:href="login_icon_hrefs[i]"></use>
      </svg>
    </div>
  </div>

  <div v-show="!isLog" class = "el-class" :class="{'move-lvzu':!isLog}">
    <div class="register-input">
      <div class="us-pw-input-pre">
        <svg class="icon bef-icon" aria-hidden="true">
          <use xlink:href="#icon-nicheng"></use>
        </svg>
          <input class = "us-pd-input" maxlength="16"  spellcheck="false" v-model="register_nickname" placeholder="‰Ω†ÁöÑÊòµÁß∞">
      </div>
      <div class="us-pw-input-pre">
        <svg class="icon bef-icon" aria-hidden="true">
          <use xlink:href="#icon-shouji"></use>
        </svg>
          <input class = "us-pd-input" maxlength="11" @blur="_verify" spellcheck="false" v-model="register_username" placeholder="ÊâãÊú∫Âè∑">
      </div>
      <div v-show="verify_code_show" class="us-pw-input-pre" :class="{'move-lvzu-2':verify_code_show}">
        <svg class="icon bef-icon" aria-hidden="true">
          <use xlink:href="#icon-yanzhengma"></use>
        </svg>
          <input class = "us-pd-input" style="width:100px;float:left;margin-left:8px;"maxlength="6" type = 'text' spellcheck="false" v-model="register_verifycode" placeholder="È™åËØÅÁ†Å">
            <el-button class="get-verifycode" type="info" @click="_getVerifyCode">Ëé∑ÂèñÈ™åËØÅÁ†Å</el-button>
      </div>
      <div class="us-pw-input-pre">
        <svg class="icon bef-icon" aria-hidden="true">
          <use xlink:href="#icon-mima1"></use>
        </svg>
          <input class = "us-pd-input"  maxlength="22" type = 'password' spellcheck="false" v-model="register_password" placeholder="ÂØÜÁ†Å">
      </div>
    </div>

  <div id="after-move">
    <div class="button">
      <el-button id="reg_but" class="login-button" type="info" size='large' @click="_register">Ê≥® ÂÜå</el-button>
    </div>
    <p class="sign-up-msg">ÁÇπÂáª ‚ÄúÊ≥®ÂÜå‚Äù Âç≥Ë°®Á§∫ÊÇ®ÂêåÊÑèÂπ∂ÊÑøÊÑèÈÅµÂÆà<br>
      <a style="color:#58B7FF;" href="#">Áî®Êà∑ÂçèËÆÆ</a>Âíå
      <a style="color:#58B7FF;" href="#">ÈöêÁßÅÊîøÁ≠ñ</a>„ÄÇ
    </p>
    <div class="others">
      <h6 class="social-text">Á§æ‰∫§Ë¥¶Âè∑Ê≥®ÂÜå</h6>
    </div>
    <div class="other-icons">
      <svg class="icon icons" aria-hidden="true" v-for="i in 4">
        <use v-bind:xlink:href="register_icon_hrefs[i]"></use>
      </svg>
    </div>
  </div>
</div>


 </div>
</div>
</template>

<script>
import mytools from '../../tools/myTools'
export default {

  name: 'Register',
  data() {
    return {
      picList: [],
      bg_url:'',
      //ÁªëÂÆöÁôªÂΩïÁïåÈù¢ÁöÑinputÂÄº
      log_username: '',
      log_password: '',
      //ÁªëÂÆö‰∫ÜÊ≥®ÂÜåÁïåÈù¢ÁöÑinputÂÄº
      register_nickname: '',
      register_username: '',
      register_password: '',
      register_verifycode: '',
      //ÊòØÂê¶ÊòØÁôªÂΩïÈ°µÈù¢ false ‰∏∫Ê≥®ÂÜåÈ°µÈù¢
      isLog: false,
      //ÁôªÂΩïÁïåÈù¢,ÊòØÂê¶ËÆ∞‰ΩèÁî®Êà∑ÂêçÂØÜÁ†ÅÈÄâÈ°π false‰∏∫Êú™ÊâìÈí©
      remember: false,
      //ÁôªÂΩïÁïåÈù¢‰∏ãÊñπÁ§æ‰∫§Ë¥¶Âè∑ÁôªÂΩïicon
      login_icon_hrefs: ['#icon-ziyuan', '#icon-shejiao-instagram', '#icon-shejiao-github', '#icon-shejiao1', '#icon-shejiao-facebook'],
      //Ê≥®ÂÜåÁïåÈù¢‰∏ãÊñπÁ§æ‰∫§Ë¥¶Âè∑Ê≥®ÂÜåicon
      register_icon_hrefs: ['#icon-ziyuan', '#icon-weibo', '#icon-shejiao-twitter', '#icon-shejiao1', '#icon-shejiao-github'],
      //Ë°®Á§∫Ê≥®ÂÜåÁïåÈù¢ÊâãÊú∫Âè∑Ê†ºÂºèÈîôËØØmessageÂºπÁ™óÊòØÂê¶ÊòØÊøÄÊ¥ªÁä∂ÊÄÅ Ëã•ÊòØÊøÄÊ¥ªÁä∂ÊÄÅÂàô‰∏ç‰ºö‰∫ßÁîü‰∫åÊ¨°ÂºπÁ™ó
      user_msg_showing: false,
      //Ë°®Á§∫ËæìÂÖ•ÊâãÊú∫È™åËØÅÁ†ÅÁöÑÊ°ÜÊòæ‰∏çÊòæÁ§∫
      verify_code_show: false,
      hostname:'47.94.129.112',
    }
  },

  mounted: function() {
    const t = this
    fetch(`http://${t.hostname}/api/get_pic_urls`, {
        method: 'get',
      })
      .then(re => re.json())
      .then(re => {
        for(let i of re.list){
          t.picList.push({
            pic_name:i.fields.pic_name,
            pic_url:i.fields.pic_url
          })
          if(i.fields.pic_name == 'background'){
            t.bg_url = i.fields.pic_url
          }
        }
      })
  },

  methods: {
    //ÁÇπÂáªÁôªÂΩï
    setLog: function() {
      if (!this.isLog) {
        this.setNull();
        this.isLog = true;
        this.verify_code_show = false;

        return;
      }
    },
    //ÁÇπÂáªÊ≥®ÂÜå
    setReg: function() {
      if (this.isLog) {
        this.setNull();
        this.isLog = false;
        return;
      }
    },
    //Êõ¥Êç¢ÁôªÂΩïÊ≥®ÂÜåÁä∂ÊÄÅÂ∞ÜÊâÄÊúâinputÊ°ÜÂÄºÊ∏ÖÁ©∫
    setNull: function() {
      this.log_username = '';
      this.log_password = '';
      this.register_nickname = '';
      this.register_username = '';
      this.register_password = '';
      this.register_verifycode = '';
    },

    _login: function() {
      const t = this
      if(t.log_username.length!=11){
        this.$message.error('ÊÇ®ÁöÑÊâãÊú∫Âè∑ËæìÂÖ•ÊúâËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•');
        t.log_username = ''
        return
      }
      fetch(`http://${t.hostname}/api/account_login`, {
          method: 'post',
          body: 'username=' + t.log_username + '&password=' + t.log_password,
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/x-www-form-urlencoded"
          },
        })
        .then(re => re.json())
        .then(re => {
          if(re.msg == 'success'){
            this.$message({
              message: 'ÁôªÂΩïÊàêÂäüÔºÅ',
              type: 'success'
            });

            setTimeout(function () {
                t.$router.push({ path:'/main' })
            },2500)

            return
          }else if (re.msg =='passwordError') {
            this.$message.error('Ë¥¶Âè∑ÂØÜÁ†ÅÊúâËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•');
            t.log_password = ''
            return
          }else if (re.msg == 'notRegisterd') {
            this.$message({
              message: 'Ê≠§Áî®Êà∑ÂêçÊú™Ê≥®ÂÜåÔºåËØ∑ÂÖàÊ≥®ÂÜå',
              type: 'warning'
            });
            t.setReg();
            return
          }
        })
    },
    _verify:function () {
      const  t = this
      if(t.register_username.length!=11){
        if(t.register_username.length == 0){
          return
        }
        this.$message.error('ÊÇ®ÁöÑÊâãÊú∫Âè∑ËæìÂÖ•ÊúâËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•');
        t.register_username = ''
        return
      }
      $(".main-part").css("height", "600px");
      $("#after-move").css("transform", "translateY(25px)");
      t.verify_code_show = true
    },
    _getVerifyCode: function() {
      const t = this
      if (t.register_username.length != 11) {
        this.$message.error('ÊÇ®ÁöÑÊâãÊú∫Âè∑ËæìÂÖ•ÊúâËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•');
        t.register_username = ''
        return
      }
      setTimeout(function() {
        t.register_verifycode = mytools._generateVCode()
      }, 3000)
    },
    _register: function() {
      const t = this
      if (t.register_nickname.length < 7) {
        this.$message.error('ÊÇ®ËæìÂÖ•ÁöÑÊòµÁß∞ËøáÁü≠ÔºåÈïøÂ∫¶Â∫îÂ§ß‰∫é6');
        return
      }
      if (t.register_username.length != 11) {
        this.$message.error('ÊÇ®ÁöÑÊâãÊú∫Âè∑ËæìÂÖ•ÊúâËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•');
        return
      }
      if (t.register_verifycode.length != 6) {
        this.$message.error('ÊÇ®ÁöÑÈ™åËØÅÁ†ÅÊúâËØØ,ËØ∑ÈáçÊñ∞Ëé∑Âèñ');
        return
      }
      fetch(`http://${t.hostname}/api/account_register`, {
          method: 'post',
          body: 'username=' + t.register_username + '&password=' + t.register_password + '&nickname=' + t.register_nickname,
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/x-www-form-urlencoded"
          },
        })
        .then(re => re.json())
        .then(re => {
          if (re.msg == 'success') {
            this.$message({
              message: 'ÊÅ≠ÂñúÊÇ®ÔºåÊ≥®ÂÜåÊàêÂäüÔºÅ',
              type: 'success'
            });
            setTimeout(function () {
              t.isLog = true
            },1000)
          } else if (re.msg == 'registerd username') {
            this.$message({
              message: 'ËØ•Áî®Êà∑ÂêçÂ∑≤Ë¢´Ê≥®ÂÜå',
              type: 'warning'
            });
          }
        })
    },
  },
}
</script>

<style lang="scss" scoped>
@import "./Register.scss"
</style>



