        }
      },120)
    },11000)

    setTimeout(function () {
      let type = 0;
      let addFlag = true;
      let pauseFlag = false;
      temp = 0;

      var interval = setInterval(function() {
      if (!pauseFlag) {
          t.str1 = t.typingStr3 + t.peopleType[type].substring(0, temp)
          if (addFlag) {
            temp++;
            if (temp == t.peopleType[type].length+1) {
              addFlag = false
              temp --;
              pauseFlag = !pauseFlag
              setTimeout(function () {
                pauseFlag = !pauseFlag
              },2000)
            }
          } else {
            temp--;
            if (temp == -1) {
              addFlag = true
              temp++;
              pauseFlag = !pauseFlag
              setTimeout(function () {
                pauseFlag = !pauseFlag
              },2000)
              type++;
              if (type == 5) {
